import React from 'react'
import { getAspectRatio } from '../../services/AspectRatio/AspectRatio';

class Photo extends React.Component {
  state = {
    loaded: false
  }

  constructor(props) {
    super(props)
  }

  async componentDidMount() {
    const { photo } = this.props
    const exif =  await photo.exif

    this.setState({ photo, exif })
  }

  render() {
    const { className } = this.props
    const { photo, exif } = this.state || this.props
    const { prethumb, title } = photo

    if ( ! this.state.photo) return (<img className={className} src={prethumb} alt={title.text} />)
    const aspectRatio = getAspectRatio(exif.imageSize)

    return (<img className={className} src={prethumb} alt={title.text} />)
  }
}

Photo.defaultProps = {
  loadOnIntersection: true,
  targetSize: 'Desktop',
  showPrethumb: true
}

export default Photo